@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@100;200;300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oxygen:wght@300;400;700&display=swap);
*:focus{box-shadow:none !important}:root{--h: 263;--s: 76%;--l: 43%;--theme-color: hsl(var(--h), var(--s), var(--l));--theme-color-alfa-10: hsl(var(--h), var(--s), var(--l), 0.1);--theme-color-alfa-25: hsl(var(--h), var(--s), var(--l), 0.25);--theme-color-alfa-50: hsl(var(--h), var(--s), var(--l), 0.5);--theme-palate-1: hsl(calc(var(--h) + 70), calc(var(--s) - 14%), calc(var(--l) + 12%));--theme-palate-2: hsl(calc(var(--h) - 178), calc(var(--s) + 0%), calc(var(--l) + 0%));--theme-palate-3: hsl(calc(var(--h) + 120), calc(var(--s) + 0%), calc(var(--l) + 0%));--theme-gradient: linear-gradient(to left, var(--theme-palate-1), var(--theme-color));--theme-gradient-alfa: linear-gradient(to left, var(--theme-color-alfa-10), var(--theme-color-alfa-50));--theme-shadow: rgba(9, 30, 66, 0.25) 0px 1px 1px, rgba(9, 30, 66, 0.13) 0px 0px 1px 1px;--navigation-width: 275px;--header-height: 60px;--theme-gutter: 15px;--theme-radius: 3px;--theme-fontInit-size: 14px;--theme-fontInit-weight: 400;--theme-transition-duration: 0.3s;--theme-transition-function: cubic-bezier(0.75, 0.94, 0.24, 1.22);--theme-transition: all calc(var(--theme-transition-duration) + 0.3s) var(--theme-transition-function);--theme-btn-min-size: 90px}body{font-weight:400;font-weight:var(--theme-fontInit-weight);font-size:14px;font-size:var(--theme-fontInit-size)}.rounded{border-radius:3px !important;border-radius:var(--theme-radius) !important}.form-control,.form-select{font-size:14px;font-size:var(--theme-fontInit-size)}.form-control:focus,.form-select:focus{border-color:hsl(263, 76%, 43%) !important;border-color:var(--theme-color) !important}.btn{font-size:14px;font-size:var(--theme-fontInit-size)}.btn.btn-erp{min-width:90px;min-width:var(--theme-btn-min-size)}.fw-semi-bold{font-weight:600}.header-height{height:60px;height:var(--header-height)}.text-theme-color{color:hsl(263, 76%, 43%);color:var(--theme-color)}.text-palate-1{color:hsl(calc(263 + 70), calc(76% - 14%), calc(43% + 12%));color:var(--theme-palate-1)}.text-palate-2{color:hsl(calc(263 - 178), calc(76% + 0%), calc(43% + 0%));color:var(--theme-palate-2)}.text-palate-3{color:hsl(calc(263 + 120), calc(76% + 0%), calc(43% + 0%));color:var(--theme-palate-3)}.bg-theme-color{background-color:hsl(263, 76%, 43%);background-color:var(--theme-color)}.bg-theme-color-alfa-10{background-color:hsl(263, 76%, 43%, 0.1);background-color:var(--theme-color-alfa-10)}.bg-theme-color-alfa-25{background-color:hsl(263, 76%, 43%, 0.25);background-color:var(--theme-color-alfa-25)}.bg-theme-color-alfa-50{background-color:hsl(263, 76%, 43%, 0.5);background-color:var(--theme-color-alfa-50)}.bg-theme-palate-1{background-color:hsl(calc(263 + 70), calc(76% - 14%), calc(43% + 12%));background-color:var(--theme-palate-1)}.bg-theme-palate-2{background-color:hsl(calc(263 - 178), calc(76% + 0%), calc(43% + 0%));background-color:var(--theme-palate-2)}.bg-theme-palate-3{background-color:hsl(calc(263 + 120), calc(76% + 0%), calc(43% + 0%));background-color:var(--theme-palate-3)}.bg-theme-gradient{background:linear-gradient(to left, hsl(calc(263 + 70), calc(76% - 14%), calc(43% + 12%)), hsl(263, 76%, 43%));background:var(--theme-gradient)}.bg-theme-gradient-alfa{background:linear-gradient(to left, hsl(263, 76%, 43%, 0.1), hsl(263, 76%, 43%, 0.5));background:var(--theme-gradient-alfa)}.theme-shadow{box-shadow:rgba(9, 30, 66, 0.25) 0px 1px 1px, rgba(9, 30, 66, 0.13) 0px 0px 1px 1px;box-shadow:var(--theme-shadow)}.border-theme-color{--bs-border-color: var(--theme-color)}.border-theme-color-alfa-25{--bs-border-color: var(--theme-color-alfa-25)}.border-theme-color-alfa-50{--bs-border-color: var(--theme-color-alfa-50)}.theme-card-title{font-size:calc(14px + 6px);font-size:calc(var(--theme-fontInit-size) + 6px)}.theme-page-title{font-size:calc(14px + 9px);font-size:calc(var(--theme-fontInit-size) + 9px)}#app-header{margin-top:15px;margin-top:var(--theme-gutter);margin-right:15px;margin-right:var(--theme-gutter);max-width:calc(100% - (275px + 15px * 2));max-width:calc(100% - (var(--navigation-width) + var(--theme-gutter) * 2));transition:all calc(0.3s + 0.3s) cubic-bezier(0.75, 0.94, 0.24, 1.22);transition:var(--theme-transition);width:100%}#app-navigation{width:275px;width:var(--navigation-width);transition:all calc(0.3s + 0.3s) cubic-bezier(0.75, 0.94, 0.24, 1.22);transition:var(--theme-transition);height:100vh;overflow-x:hidden}#dashboard{padding-top:calc(60px + 15px * 2);padding-top:calc(var(--header-height) + var(--theme-gutter) * 2);padding-left:calc(275px + 15px);padding-left:calc(var(--navigation-width) + var(--theme-gutter));padding-right:15px;padding-right:var(--theme-gutter);transition:all calc(0.3s + 0.42s) cubic-bezier(0.75, 0.94, 0.24, 1.22);transition:all calc(var(--theme-transition-duration) + 0.42s) var(--theme-transition-function)}

#app-navigation .nav .nav-link{padding-top:8px;padding-bottom:8px;padding-left:0;color:var(--bs-gray-200);display:flex;align-items:center;grid-column-gap:14px;-webkit-column-gap:14px;column-gap:14px;width:100%;transition:all 0.3s linear;font-size:var(--theme-fontInit-size)}#app-navigation .nav .nav-link>svg{font-size:calc(var(--theme-fontInit-size) + 4px)}#app-navigation .nav .nav-link:hover,#app-navigation .nav .nav-link:focus{background-color:transparent;color:var(--bs-gray-200)}#app-navigation .nav .dropdown{width:100%}#app-navigation .nav .dropdown .dropdown-menu{--bs-dropdown-min-width: 100%;--bs-dropdown-padding-x: 6px;--bs-dropdown-padding-y: 0;--bs-dropdown-bg: transparent;--bs-dropdown-border-width: 0;--bs-dropdown-border-radius: 0;-webkit-transform:initial !important;margin-left:0px;border-radius:0;transform:initial !important;position:static !important;top:0}#app-navigation .nav .dropdown .dropdown-menu .dropdown-menu{padding-left:calc(var(--bs-dropdown-padding-x) * 2.5)}#app-navigation .nav .dropdown .dropdown-menu .dropdown-item{color:var(--bs-gray-200);font-size:12px;padding-top:4px;padding-bottom:4px;transition:all 0.3s linear;display:flex;align-items:center;grid-column-gap:8px;-webkit-column-gap:8px;column-gap:8px;padding-right:calc(var(--bs-dropdown-padding-x) * 1.75)}#app-navigation .nav .dropdown .dropdown-menu .dropdown-item:hover,#app-navigation .nav .dropdown .dropdown-menu .dropdown-item:focus{background-color:transparent;color:var(--bs-gray-200)}#app-navigation .nav .dropdown .dropdown-toggle::after{margin-left:auto}

